<template>
  <div class="editresult" style="text-align:center; width:270px; margin:auto">
    <p class="p18" style="margin-top:20px">{{erMessage}}</p>
    <div v-if="erTweetBody!=''">
      Twitterにシェアする？<br>
      <textarea id="story" name="story" rows="3" style="width:100%" v-model="erTweetBody"></textarea>
      <a href="https://twitter.com/intent/tweet?button_hashtag=LoveTwitter&ref_src=twsrc%5Etfw" class="twitter-hashtag-button" data-show-count="false">
        Tweet #LoveTwitter
      </a>
      <a href="https://twitter.com/intent/tweet?button_hashtag=LoveTwitter&ref_src=twsrc%5Etfw" class="btn" :class="this.$root.theme_bc_1" style="width:144px">Twitterにシェア</a>
    </div>
  </div>
  <router-link to="/postlist" class="btn" :class="this.$root.theme_bc_1" style="width:144px">投稿一覧へ</router-link>
</template>

<script>
import axios from 'axios'

export default {
  name: 'EditResult',
  props: {
    type : String,
    author : String,
    title : String,
    postId : String
  },
  components: {
  },
  data() {
    return {
      erType : "",
      erMessage : "",
      erTweetBody : ""
    };
  },
  computed : {
  },
  methods : {
    initAction : function () {
      axios.get(this.$root.SERVER_PATH + 'getBaseInfo')
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }

        this.erType = this.$route.params.type;
        console.log(this.$route.params);
        if (this.type == "new") {
          //新規投稿
          this.erMessage = "投稿が完了しました";
        } else if (this.type == "update") {
          //更新
          this.erMessage = "更新が完了しました";
        } else if (this.type == "delete") {
          //削除
          this.erMessage = "削除が完了しました";
        }

        // if (this.title != "") {
        //   this.erTweetBody = this.author + "/" + this.title + "\n" + "http://" + this.postId;
        // }
      }).catch((error) => {
        console.log(error.response);
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
      vm.$root.setCurrentPage("");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
      vm.$root.setCurrentPage("");
    });
  }
}
</script>

