<template>
  <div class="registrationTwitter" style="text-align:center; width:270px; margin:auto">
    <div class="sp_line_m" />

    <p class="p18">Twitterアカウントで新規登録</p>

    <div class="sp_line_m" />

    <div id="CHK" :class="this.$root.theme_chk_1" style="display:flex">
      <label class="typeA-checkbox">
        <input type="checkbox"  v-model="isTerms" true-value="1" false-value="0">
        <div class="indicator"></div>
      </label>
      <span><a href="">利用規約</a>に同意しますか？</span>
    </div>

    <div class="sp_line_m" />

    <span class="errMsg" v-if="errorMessageData.twitter">▼ {{errorMessageData.twitter}}<br></span>
    <span v-show="isTerms == 1" class="btn btn-icon" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="pushRegist">Twitterアカウントで新規登録</span>
    <span v-show="isTerms == 0" class="btn btn-gray btn-icon" style="width:100%;">Twitterアカウントで新規登録</span>

    <div class="sp_line_m" />

    <p class="p14">既に登録済みの方はこちら</p>

    <div class="sp_line_s" />

    <span class="btn btn-yellow" style="width:100%" v-on:click="pushLogin">ログイン</span>
    <br>
    <br>
    <br>
    <br>
  </div>
</template>

<script>
import { getAuth, setPersistence, TwitterAuthProvider, signInWithPopup, browserLocalPersistence } from 'firebase/auth'
import axios from 'axios'

export default {
  name: 'RegistrationTwitter',
  components: {
  },
  data() {
    return {
      isTerms : 0,

      errorMessageData : {
        twitter : "",
      },

      kariMail : "",
    };
  },
  computed : {
  },
  methods : {
    pushTerms : function () {
      if (this.isTerms == 0) {
        this.isTerms = 1;
      } else {
        this.isTerms = 0;
      }
    },
    pushRegist : function () {
      this.errorMessageData.twitter = "";

      const provider = new TwitterAuthProvider()
      const auth = getAuth();
      setPersistence(auth, browserLocalPersistence)
      .then(() => {
        return signInWithPopup(auth, provider)
        .then((result) => {
          console.log(result);
          let params = new URLSearchParams();
          params.append('twitterId', result.user.providerData[0].uid);
          params.append('twitterName', result._tokenResponse.displayName);
          params.append('twitterIconUrl', result._tokenResponse.photoUrl);
          params.append('twitterCode', result._tokenResponse.screenName);
          params.append('twitterToken', result._tokenResponse.oauthAccessToken);
          params.append('twitterTokenSecret', result._tokenResponse.oauthTokenSecret);
          
          this.regist(params);
        }).catch((error) => {
          console.log(error.response);
        });
      }).catch((error) => {
        console.log(error.response);
      });
    },
    regist : function (params) {
      axios.get(this.$root.CSRF_URL).then(response => {
        console.log(response);
        axios.post(this.$root.SERVER_PATH + "registerTwitter", params)
        .then((response) => {
          let resData = response.data;
          if (resData["status"] == this.$root.API_FAILED_DUPLICATE) {
            //データなし
            this.errorMessageData.twitter = "このTwitterアカウントはすでに登録されています";
            return;
          }

          if (resData["status"] == this.$root.API_FAILED_LOGIN) {
            //IDとPASSでログイン失敗
            this.errorMessageData.login = "ログインに失敗しました";
            return;
          }

          if (!this.$root.checkServerResponse(response, false)) {
            return;
          }

          this.$root.isLogin = true;
          this.$router.push('/mainmenu');
        })
        .catch((e) => {
          alert(e); 
        });
      });
    },
    pushLogin : function () {
      this.$router.push("/login");
    },
  },
  // beforeRouteEnter(to, from, next) {
  //   next(vm => {
  //   });
  // },
  // beforeRouteUpdate(to, from, next) {
  //   next(vm => {
  //   });
  // }
}
</script>

