<script>
import axios from 'axios'

export default {
  name: 'Edit',
  components: {
  },
  data() {
    return {
      edPostId : "",
      edTitle : "",
      edOutline : "",
      edBody : "",
      edConversionList : [["",""], ["",""], ["",""]],
