<template>
  <div class="login" style="text-align:center; width:270px; margin:auto">
    <div class="sp_line_m" />
    
    <p class="p18">ID（メールアドレス）とPASSでログイン</p>

    <div class="sp_line_m" />

    <div style="text-align:left">
      <span class="errMsg" v-if="errorMessageData.id">▼ {{errorMessageData.id}}<br></span>
      <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" style="width:100%" v-model="id" maxlength="200" :placeholder="this.$root.PLACEHOLDER_MAIL"><br>
      <div class="sp_line_m" />
      
      <span class="errMsg" v-if="errorMessageData.password">▼ {{errorMessageData.password}}<br></span>
      <input type="password" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" style="width:100%" v-model="password" maxlength="32" :placeholder="this.$root.PLACEHOLDER_PASSWORD"><br>
    </div>

    <div class="sp_line_s" />

    <span class="errMsg" v-if="errorMessageData.login">▼ {{errorMessageData.login}}<br></span>
    <span class="btn btn-icon btn-icon-key" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="pushIDLogin">IDとPASSでログイン</span>
    <div class="sp_line_m" />

    <span class="link" :class="this.$root.theme_fc_1" v-on:click="pushReissue">パスワードを忘れた方はこちら</span>

    <div class="sp_line_m" />

    <p class="p14">初めての方はこちら</p>

    <div class="sp_line_s" />

    <span class="btn btn-yellow" style="width:100%" v-on:click="pushRegMail">IDとPASSで新規登録</span>

    <div class="sp_line_xl" />

    <hr style="border: 1px solid; margin: 0" class="theme_boc_1">
    
    <div class="sp_line_m" />

    <p class="p18">Twitterアカウントでログイン</p>

    <div class="sp_line_m" />

    <span class="errMsg" v-if="errorMessageData.twitter">▼ {{errorMessageData.twitter}}<br></span>
    <span class="btn btn-icon btn-icon-twitter" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="pushTwitterLogin">ログイン</span>

    <div class="sp_line_m" />
    
    <p class="p14">初めての方はこちら</p>

    <div class="sp_line_m" />

    <span class="btn btn-yellow btn-icon btn-icon-twitter" style="width:100%" v-on:click="pushRegTwitter">Twitterで新規登録</span>
    <br>
    <br>
    <br>
    <br>
  </div>
</template>

<script>
import { getAuth, setPersistence, TwitterAuthProvider, signInWithPopup, browserLocalPersistence } from 'firebase/auth'
import axios from 'axios'

export default {
  name: 'Login',
  components: {
  },
  data() {
    return {
      id : "",
      password : "",

      errorMessageData : {
        id : "",
        password : "",
        login : "",
        twitter : "",
      }
    };
  },
  computed : {
  },
  methods : {
    pushIDLogin : function () {
      console.log("IN");
      this.errorMessageData.id = "";
      this.errorMessageData.password = "";
      this.errorMessageData.login = "";

      //トリム
      this.id = this.id.trim();
      this.password = this.password.trim();
      
      //入力チェック
      if (this.id == "") {
        this.errorMessageData.id = "ID（メールアドレス）の文字数は1文字以上入力してください";
      } else if (this.id.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.id = "ID（メールアドレス）の文字数は200文字以下で入力してください";
      } else {
        let val = this.$root.checkAddress(this.id);
        if (val == 1) {
          this.errorMessageData.id = "ID（メールアドレス）に誤りがあります。もう一度入力してください";
        } else if (val == 2) {
          this.errorMessageData.id = "該当のメールアドレスのドメインでは登録できません。別のメールアドレスでご登録ください";
        }
      }
      
      if (this.password.length < 8) {
        this.errorMessageData.password = "パスワードの文字数は8文字以上入力してください";
      } else if (this.password.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.password = "パスワードの文字数は32文字以下で入力してください";
      } else if (this.$root.checkPassword(this.password)) {
        this.errorMessageData.password = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.errorMessageData.id != "" || this.errorMessageData.password != "") {
        return;
      }
      
      let params = new URLSearchParams();
      params.append('mailAddress', this.id);
      params.append('password', this.password);
      let method = "m_login";

      this.login(params, method);
    },
    pushTwitterLogin : function () {
      this.signinTwitter();
    },
    pushReissue : function () {
      this.$router.push('/reissuePassword');
    },
    pushRegMail : function () {
      this.$router.push('/registration');
    },
    pushRegTwitter : function () {
      this.$router.push('/registrationTwitter');
    },
    signinTwitter : function () {
      this.errorMessageData.twitter = "";

      const provider = new TwitterAuthProvider()
      const auth = getAuth();
      setPersistence(auth, browserLocalPersistence)
      .then(() => {
        return signInWithPopup(auth, provider)
        .then((result) => {
          console.log(result);
          let params = new URLSearchParams();
          params.append('twitterId', result.user.providerData[0].uid);
          params.append('twitterName', result._tokenResponse.displayName);
          params.append('twitterIconUrl', result._tokenResponse.photoUrl);
          params.append('twitterCode', result._tokenResponse.screenName);
          params.append('twitterToken', result._tokenResponse.oauthAccessToken);
          params.append('twitterTokenSecret', result._tokenResponse.oauthTokenSecret);
          let method = "t_login";

          this.login(params, method);
        }).catch((error) => {
          console.log(error.response);
        });
      }).catch((error) => {
        console.log(error.response);
      });
    },
    login : function (params, method) {
      axios.get(this.$root.CSRF_URL).then(response => {
        console.log(response);
        axios.post(this.$root.SERVER_PATH + method, params)
        .then((response) => {
          let resData = response.data;
          if (method == "t_login" && resData["status"] == this.$root.API_FAILED_NODATA) {
            //データなし
            this.errorMessageData.twitter = "このTwitterアカウントは登録されていません";
            return;
          }

          if (resData["status"] == this.$root.API_FAILED_LOGIN) {
            //IDとPASSでログイン失敗
            this.errorMessageData.login = "ログインに失敗しました";
            return;
          }

          if (!this.$root.checkServerResponse(response, false)) {
            return;
          }

          this.$root.isLogin = true;
          this.$router.push('/mainmenu');
        })
        .catch((e) => {
          alert(e); 
        });
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.mailAddress = "";
      vm.password = "";
      vm.$root.setCurrentPage("LOGIN");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.mailAddress = "";
      vm.password = "";
      vm.$root.setCurrentPage("LOGIN");
    });
  }
}
</script>

