            <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" maxlength="10" v-model="setUserName" style="width:100%" placeholder="ユーザー名を入力してください">
            <div class="sp_line_m" />

