            <div class="grayBar" />

            <p class="k_mi">お気に入りタグ<img class="cursor tips" v-on:click="this.$root.pushTips(6)" :src="this.$root.IMG_PATH + 'btn_hint' + this.$root.theme_img"></p>
            <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" maxlength="170" v-model="setFavoriteTag" style="width:100%" placeholder="例）タグ①,タグ②">
            <p class="errMsg" v-if="errorMessageData.favorite"><br>{{errorMessageData.favorite}}</p>
            <div class="sp_line_s" />
            <span class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushUpdateFavorite">変更</span>

            <div class="sp_line_m" />
            
            <p class="k_mi">ミュートタグ<img class="cursor tips" v-on:click="this.$root.pushTips(7)" :src="this.$root.IMG_PATH + 'btn_hint' + this.$root.theme_img"></p>
            <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" maxlength="170" style="width:100%" v-model="setMuteTag" placeholder="例）タグ①,タグ②">
            <p class="errMsg" v-if="errorMessageData.mute"><br>{{errorMessageData.mute}}</p>
            <div class="sp_line_s" />
            <span class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushUpdateMute">変更</span>
            
            <div class="grayBar" />
            
            <p class="k_mi">ユーザーミュート<img class="cursor tips" v-on:click="this.$root.pushTips(8)" :src="this.$root.IMG_PATH + 'btn_hint' + this.$root.theme_img"></p>
            <span class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushMuteUser">確認する</span>
            
            <div class="grayBar" />
            
            <p class="k_mi">Twitterアカウント</p>
            <div v-show="this.$root.userData.isAuthTwitter">
              <p class="link" :class="this.$root.theme_fc_1" v-on:click="pushBtn('TWITTER_LINK')">@{{ this.setTwitterCode }}</p>
              <div class="sp_line_s" />

              <span v-show="this.$root.userData.isAuthMail" class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushLiftCertification(0)">認証解除</span>
              <span v-show="!this.$root.userData.isAuthMail" class="btn btn-gray" style="width:145px">認証解除</span>
            </div>
            <div v-show="!this.$root.userData.isAuthTwitter">
              <span class="link" :class="this.$root.theme_fc_1">未認証</span><br>
              <div class="sp_line_s" />

              <p class="errMsg" v-if="errorMessageData.resChMail">{{errorMessageData.resCertificateTwitter}}</p>
              <span class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushCertificateTwitter">認証する</span>
            </div>
            <br>
            <p class="k_mi">Twitter連携</p>
            〇〇のすべてのデータは現在連携中のTwitterアカウントに紐付きます<br>
            複数アカウントが必要な場合は、新たにTwitterアカウントを取得して、当サービスにログインしなおしてください<br>
            Twitterアカウントを誤って削除してしまった場合や、Twitterアカウントにログインできなくなった場合は、当サービスで対応することはできません<br>
            新たにTwitterアカウントを取得し、当サービスにご登録お願いいたします
            <div class="sp_line_m" />

            <p class="k_mi">プロフィールにTwitterアカウントを表示する</p>
            <div class="switchArea" :class="theme_switch">
              <input type="checkbox" id="switchSe" v-model="setShowTwitter" @change="changeShowTwitter">
              <label for="switchSe"><span></span></label>
              <div id="swImg"></div>
            </div>

            <div class="grayBar" />
            
            <p class="k_mi">メールアカウント</p>
            <div v-show="this.isAuthMail">
              <p>{{ this.setMailAddress }}</p>

              <span v-show="this.$root.userData.isAuthTwitter" class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushLiftCertification(1)">認証解除</span>
              <span v-show="!this.$root.userData.isAuthTwitter" class="btn btn-gray" style="width:145px">認証解除</span>
              <div class="sp_line_m" />

              <p class="k_mi">メールアドレス変更</p>
              <p class="errMsg" v-if="errorMessageData.chMailAddress">{{errorMessageData.chMailAddress}}</p>
              <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="chMailAddress" autocomplete="off" style="width:100%" maxlength="200" :placeholder="this.$root.PLACEHOLDER_MAIL">              
              <div class="sp_line_m" />

              <p class="errMsg" v-if="errorMessageData.resChMail">{{errorMessageData.resChMail}}</p>
              <span class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushChangeMail">変更する</span>
              <div class="sp_line_m" />
              
              <p class="k_mi">パスワード変更</p>
              <p class="k_mi">現在のパスワード</p>
              <p class="errMsg" v-if="errorMessageData.currentPassword">{{errorMessageData.currentPassword}}</p>
              <input type="password" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="currentPassword" style="width:100%" maxlength="32" autocomplete="off"
               :placeholder="this.$root.PLACEHOLDER_PASSWORD"><br>
              <div class="sp_line_s" />
              
              <p class="k_mi">新しいパスワード</p>
              <p class="errMsg" v-if="errorMessageData.newPassword1">{{errorMessageData.newPassword1}}</p>
              <input type="password" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="newPassword1" style="width:100%" maxlength="32" autocomplete="off"
               :placeholder="this.$root.PLACEHOLDER_PASSWORD"><br>
              <div class="sp_line_s" />
              
              <p class="k_mi">新しいパスワード（確認）</p>
              <p class="errMsg" v-if="errorMessageData.newPassword2">{{errorMessageData.newPassword2}}</p>
              <input type="password" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="newPassword2" style="width:100%" maxlength="32" autocomplete="off"
               :placeholder="this.$root.PLACEHOLDER_PASSWORD"><br>
              <div class="sp_line_s" />

              <p class="errMsg" v-if="errorMessageData.resChPassword">{{errorMessageData.resChPassword}}</p>
              <p class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushChangePassword">変更する</p>
            </div>
            <div v-show="!this.$root.userData.isAuthMail">
              <p class="link" :class="this.$root.theme_fc_1">未認証</p>
              <div class="sp_line_m" />

              <p class="k_mi">メールアドレス</p>
              <p class="errMsg" v-if="errorMessageData.mailAddress">{{errorMessageData.mailAddress}}</p>
              <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="authMailAddress" style="width:100%" maxlength="200" autocomplete="new-password"
               :placeholder="this.$root.PLACEHOLDER_MAIL"><br>              
              <div class="sp_line_s" />

              <p class="k_mi">パスワード</p>
              <p class="errMsg" v-if="errorMessageData.password">{{errorMessageData.password}}</p>
              <input type="password" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="authPassword" style="width:100%" maxlength="32" autocomplete="new-password"
               :placeholder="this.$root.PLACEHOLDER_PASSWORD"><br>
              <div class="sp_line_s" /> 

              <p class="errMsg" v-if="errorMessageData.resAuthMail">{{errorMessageData.resAuthMail}}</p>
              <span class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushCertificateMail">認証する</span>
            </div>

            <div class="grayBar" />

            <p class="k_mi">お知らせ</p>
            新しい機能を追加したときや、サービスに関する情報をメールで送信します<br>
            設定をOFFにしても重要なお知らせはメールいたします

            <div class="grayBar" />

            <p class="k_mi">退会</p>
            すべてのデータが削除されます<br>
            本当に退会してもよろしいですか？
              <div class="sp_line_s" />
            <span class="btn btn-gray" style="width:145px" v-on:click="pushBtn('WITHDRAWAL')">退会</span>
          </div>
        </div>
      </div>
    </div>
    <popup-base ref="popup" @popupAction="popupAction" :kbn="this.popupKbn" :params="this.popupParams"></popup-base>
  </div>
</template>

<script>
import { getAuth, setPersistence, TwitterAuthProvider, signInWithPopup, browserLocalPersistence } from 'firebase/auth'
import axios from 'axios'

export default {
  name: 'Setting',
  components: {
  },
  data() {
    return {
      setUserName : "",
      setProfile : "",
      setMuteUserList : {},
      setFavoriteTag : "",
      setMuteTag : "",
      setIconFileName : "",
      setIconImg : "",
      setIconFile : null,
      setTwitterCode : "",
      setMailAddress : "",
      setShowTwitter : false,

      chMailAddress : "",
      authMailAddress : "",
      authPassword : "",
      authNumbers : "",

      currentPassword : "",
      newPassword1 : "",
      newPassword2 : "",

      isChangeIcon : false,
      liftIndex : -1,

      errorMessageData : {
        icon : "",
        userName : "",
        profile : "",
        favorite : "",
        mute : "",

        mailAddress : "",
        password : "",
        numbers : "",
        chMailAddress: "",
        currentPassword : "",
        newPassword1 : "",
        newPassword2 : "",

        resChMail : "",
        resCertificateTwitter : "",
        resAuthMail : "",
        resChPassword : "",
      },

      popupKbn : "",
      popupParams : {
        title : "",
        body : "",
        tweet : "",
        muteUserList : [],
      },
    };
  },
  computed : {
    setMypageUrl : function() {
      return "http://secretdream.com/" + this.$root.userData.userId;
    },
    theme_switch : function() {
        if (this.$root.themeIndex == 0) {
          return "theme_switch_1";
        } else if (this.$root.themeIndex == 1) {
          return "theme_switch_2";
        } else if (this.$root.themeIndex == 2) {
          return "theme_switch_3";
        }
        return "theme_switch_1";
    }
  },
  methods : {
    pushCertificateMail : function () {
      this.errorMessageData.mailAddress = "";
      this.errorMessageData.password = "";
      this.errorMessageData.resAuthMail = "";

      //トリム
      this.authMailAddress = this.authMailAddress.trim();
      this.authPassword = this.authPassword.trim();
      
      //入力チェック
      if (this.authMailAddress == "") {
        this.errorMessageData.mailAddress = "ID（メールアドレス）の文字数は1文字以上入力してください";
      } else if (this.authMailAddress.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.mailAddress = "ID（メールアドレス）の文字数は200文字以下で入力してください";
      } else {
        let val = this.$root.checkAddress(this.authMailAddress);
        if (val == 1) {
          this.errorMessageData.mailAddress = "ID（メールアドレス）に誤りがあります。もう一度入力してください";
        } else if (val == 2) {
          this.errorMessageData.mailAddress = "該当のメールアドレスのドメインでは登録できません。別のメールアドレスでご登録ください";
        }
      }
      
      if (this.authPassword.length < 8) {
        this.errorMessageData.password = "パスワードの文字数は8文字以上入力してください";
      } else if (this.authPassword.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.password = "パスワードの文字数は32文字以下で入力してください";
      } else if (this.$root.checkPassword(this.authPassword)) {
        this.errorMessageData.password = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.errorMessageData.mailAddress != "" || this.errorMessageData.password != "") {
        return;
      }
      
      //仮登録処理
      let params = new URLSearchParams();
      params.append('mailAddress', this.authMailAddress);
      params.append('password', this.authPassword);
      params.append('kbn', "ADD");
      //axios.get('/sanctum/csrf-cookie').then(response => {
      axios.post(this.$root.SERVER_PATH + 'reqAuthMail', params)
      .then((response) => {
        let resData = response.data;

        if (resData["status"] == this.$root.API_FAILED_DUPLICATE) {
          //仮登録エラー（本登録重複）
          this.errorMessageData.resAuthMail = "このメールアドレスは既に使用されています";
          return;
        }

        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }

        //ポップアップを出す
        this.popupKbn = "CERTIFICATION";
        this.popupParams = {
          title : "認証用メール発行",
          body : "アカウント登録の確認メールを送信しました。受信したメールアドレスに記載されているURLより、手続きを完了してください。",
        }
        console.log("http://localhost:8001/#/registrationConfirmation/" + resData["temp_id"]);
        this.$refs.popup.showModal();
      }).catch((e) => {
        alert(e);
      });
    },
    pushChangeMail : function () {

      this.errorMessageData.chMailAddress = "";
      this.errorMessageData.resChMail = "";
      //トリム
      this.chMailAddress = this.chMailAddress.trim();

      if (this.chMailAddress == "") {
        this.errorMessageData.chMailAddress = "ID（メールアドレス）の文字数は1文字以上入力してください";
      } else if (this.chMailAddress.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.chMailAddress = "ID（メールアドレス）の文字数は200文字以下で入力してください";
      } else {
        let val = this.$root.checkAddress(this.chMailAddress);
        if (val == 1) {
          this.errorMessageData.chMailAddress = "ID（メールアドレス）に誤りがあります。もう一度入力してください";
        } else if (val == 2) {
          this.errorMessageData.chMailAddress = "該当のメールアドレスのドメインでは登録できません。別のメールアドレスでご登録ください";
        }
      }

      //仮登録処理
      let params = new URLSearchParams();
      params.append('mailAddress', this.chMailAddress);
      params.append('kbn', "CHANGE");
      //axios.get('/sanctum/csrf-cookie').then(response => {
      axios.post(this.$root.SERVER_PATH + 'reqAuthMail', params)
      .then((response) => {
        let resData = response.data;

        if (resData["status"] == this.$root.API_FAILED_DUPLICATE) {
          //仮登録エラー（本登録重複）
          this.errorMessageData.resChMail = "このメールアドレスは既に使用されています";
          return;
        }

        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }

        //ポップアップを出す
        this.popupKbn = "CERTIFICATION";
        this.popupParams = {
          title : "ID変更用メール発行",
          body : "ID変更用の確認メールを送信しました。受信したメールアドレスに記載されているURLより、手続きを完了してください。",
        }
        console.log("http://localhost:8001/#/registrationConfirmation/" + resData["temp_id"]);
        this.$refs.popup.showModal();
      }).catch((e) => {
        alert(e);
      });
    },
    pushCertificateTwitter : function () {
      this.errorMessageData.resCertificateTwitter = "";

      const provider = new TwitterAuthProvider()
      const auth = getAuth();
      setPersistence(auth, browserLocalPersistence)
      .then(() => {
        return signInWithPopup(auth, provider)
        .then((result) => {
          let params = new URLSearchParams();
          params.append('twitterId', result._tokenResponse.localId);
          params.append('twitterCode', result._tokenResponse.screenName);
          params.append('twitterToken', result._tokenResponse.oauthAccessToken);
          params.append('twitterTokenSecret', result._tokenResponse.oauthTokenSecret);

          axios.post(this.$root.SERVER_PATH + 'addAuthTwitter', params)
          .then((response) => {
            let resData = response.data;
            if (resData["status"] == this.$root.API_FAILED_DUPLICATE) {
              //パスワード不一致
              this.errorMessageData.resCertificateTwitter = "このTwitterアカウントは既に使用されています";
              console.log(resData);
              return;
            }

            if (!this.$root.checkServerResponse(response, true)) {
              return;
            }
          }).catch((e) => {
            alert(e);
          });
        }).catch((error) => {
          console.log(error.response);
        });
      }).catch((error) => {
        console.log(error.response);
      });
    },
    pushChangePassword : function () {
      this.errorMessageData.currentPassword = "";
      this.errorMessageData.newPassword1 = "";
      this.errorMessageData.newPassword2 = "";
      this.errorMessageData.resChPassword = "";
      //トリム
      this.currentPassword = this.currentPassword.trim();
      this.newPassword1 = this.newPassword1.trim();
      this.newPassword2 = this.newPassword2.trim();
      
      //入力チェック
      if (this.currentPassword.length < 8) {
        this.errorMessageData.currentPassword = "現在のパスワードの文字数は8文字以上入力してください";
      } else if (this.currentPassword.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.currentPassword = "パスワードの文字数は32文字以下で入力してください";
      } else if (this.$root.checkPassword(this.currentPassword)) {
        this.errorMessageData.password = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.newPassword1.length < 8) {
        this.errorMessageData.newPassword1 = "新しいパスワードの文字数は8文字以上入力してください";
      } else if (this.newPassword1.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.newPassword1 = "パスワードの文字数は32文字以下で入力してください";
      } else if (this.$root.checkPassword(this.newPassword1)) {
        this.errorMessageData.password = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.newPassword2.length < 8) {
        this.errorMessageData.newPassword2 = "新しいパスワード（確認）の文字数は8文字以上入力してください";
      } else if (this.newPassword2.length > this.$root.LENGTH_PASSWORD) {
        this.errorMessageData.newPassword2 = "パスワードの文字数は32文字以下で入力してください";
      } else if (this.$root.checkPassword(this.newPassword2)) {
        this.errorMessageData.password = "パスワードに使用できない文字が記入されています。もう一度入力してください";
      }

      if (this.errorMessageData.currentPassword != "" || this.errorMessageData.newPassword1 != "" || this.errorMessageData.newPassword2 != "") {
        return;
      }
      if (this.newPassword1 != this.newPassword2) {
        this.errorMessageData.newPassword1 = "正しい新しいパスワード（確認）を入力してください";
        return;
      }
      if (this.currentPassword == this.newPassword1) {
        this.errorMessageData.newPassword1 = "現在のパスワードと異なる値を入力してください";
        return;
      }

      //変更処理
      let params = new URLSearchParams();
      params.append('currentPassword', this.currentPassword);
      params.append('newPassword', this.newPassword1);
      //axios.get('/sanctum/csrf-cookie').then(response => {
      axios.post(this.$root.SERVER_PATH + 'updateAuthPassword', params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_MISMATCH) {
          //パスワード不一致
          this.errorMessageData.resChPassword = "現在のパスワードが正しくありません";
          console.log(resData);
          return;
        }

        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }

        this.popupKbn = "CERTIFICATION";
        this.popupParams = {
          title : "変更完了",
          body : "パスワードを変更しました",
        }
        this.$refs.popup.showModal();

        this.currentPassword = "";
        this.newPassword1 = "";
        this.newPassword2 = "";
      }).catch((e) => {
        alert(e);
      });
    },
    popupAction: function(val) {
      if (val == 'WITHDRAWAL') {
        //退会
        let params = new URLSearchParams();
        axios.post(this.$root.SERVER_PATH + 'deleteUser', params)
        .then((response) => {
          if (!this.$root.checkServerResponse(response, true)) {
            return;
          }
          this.$refs.dialog_withdrawal.style.display = "none";
          this.$root.Logout(false);
        });
      } else if (val == 'DELETE_CERTIFICATION') {
        //認証解除
        if (this.liftIndex == 0) {
          //twitter解除
          let params = new URLSearchParams();
          
          //axios.get('/sanctum/csrf-cookie').then(response => {
          axios.post(this.$root.SERVER_PATH + 'delAuthTwitter', params)
          .then((response) => {
            if (!this.$root.checkServerResponse(response, true)) {
              return;
            }
          }).catch((e) => {
            alert(e);
          });
        } else if (this.liftIndex == 1) {
          //メール解除
          let params = new URLSearchParams();
          
          //axios.get('/sanctum/csrf-cookie').then(response => {
          axios.post(this.$root.SERVER_PATH + 'delAuthMail', params)
          .then((response) => {
            if (!this.$root.checkServerResponse(response, true)) {
              return;
            }
            this.currentPassword = "";
            this.newPassword1 = "";
            this.newPassword2 = "";
            this.mailAddress = "";
            this.password = "";
          }).catch((e) => {
            alert(e);
          });
        }
      }
    },
    onFileChange(e) {
      this.errorMessageData.icon = "";
      const files = e.target.files || e.dataTransfer.files;
      let size = files[0].size;
      let type = files[0].type;

      //チェック
      if (size > 1024000) {
        //サイズオーバー
        this.errorMessageData.icon = "アイコンのサイズは1MB以下にしてください";
        return;
      }
      if (type != "image/jpeg" && type != "image/png") {
        //形式エラー
        this.errorMessageData.icon = "アイコンの形式はpngまたはjpegにしてください";
        return;
      }
      console.log(size + " " + type);

      this.createImage(files[0]);
      this.setIconFile = files[0];
      this.setIconFileName = files[0].name;
      this.isChangeIcon = true;
    },
    createImage(file) {
      const reader = new FileReader();
      reader.onload = e => {
        this.setIconImg = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    remove() {
      this.uploadedImage = false;
    },
    pushLiftCertification : function(val) {
      this.liftIndex = val;

      //確認ウィンドウを出す
      this.popupKbn = "DELETE_CERTIFICATION";
      this.popupParams = {
        title : "認証解除",
        body : "本当によろしいですか？",
      }
      this.$refs.popup.showModal();
    },
    pushBtn : function(name) {
      if (name == "TWITTER_LINK") {
        window.open("https://twitter.com/" + this.setTwitterCode, '_blank');
      } else if (name == "ICON_DEFAULT") {
        this.setIconFileName = "default";
        this.setIconImg = "";
        this.setIconFile = null;
        this.isChangeIcon = true;
      } else if (name == "MUTE_USER") {
        //ミュートユーザーポップアップを出す
        this.popupKbn = "MUTE_USER";
        this.popupParams = {
          title : "ミュートユーザー",
          body : "",
          muteUserList : this.setMuteUserList
        }
        this.$refs.popup.showModal();
      }
    },
    changeShowTwitter : function () {
      // console.log("change " + this.setShowTwitter);
      //twitter表示の変更
      let params = new URLSearchParams();      
      params.append('showTwitter', this.setShowTwitter);
      axios.post(this.$root.SERVER_PATH + 'updateSettingShowTwitter', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        //エラーだったら戻す？

        this.popupKbn = "UPDATE";
        this.popupParams = {
          title : "Twitterアカウント表示",
          body : "変更しました",
        }
        this.$refs.popup.showModal();
      }).catch((e) => {
        alert(e);
      });

    },
    pushUpdateProfile : function () {
      //トリム
      this.setUserName = this.setUserName.trim();
      this.setProfile = this.setProfile.trim();

      //前回と同じか
      if (this.setUserName == this.$root.userData.userName && this.setProfile == this.$root.userData.profile && !this.isChangeIcon) {
        return;
      }

      //入力チェック
      this.errorMessageData.icon = "";
      this.errorMessageData.userName = "";
      this.errorMessageData.profile = "";
      
      //ユーザー名
      if (this.setUserName == "") {
        this.errorMessageData.userName = "ユーザー名は1文字以上入力してください";
      } else if (this.setUserName.length > 10) {
        this.errorMessageData.userName = "ユーザー名は10文字以下で入力してください";
      } else {
          let regStr = this.$root.checkRegulation(this.setUserName)  
          if (regStr != "") {
            this.edErrorMessageData.userName = this.$root.STR_ERROR_REGULATION + regStr;
          }
        }

      //プロフィール
      if (this.setProfile == "") {
        this.errorMessageData.profile = "自己紹介名は1文字以上入力してください";
      } else if (this.setProfile.length > 400) {
        this.errorMessageData.profile = "自己紹介は400文字以下で入力してください";
      } else {
          let regStr = this.$root.checkRegulation(this.setProfile)  
          if (regStr != "") {
            this.edErrorMessageData.profile = this.$root.STR_ERROR_REGULATION + regStr;
          }
        }

      //アイコンファイル？

      if (this.errorMessageData.userName != "" || this.errorMessageData.profile != "" || this.errorMessageData.icon != "") {
        //エラー
        return;
      } 

      let params = new FormData();
      params.append('userName', this.setUserName);
      params.append('profile', this.setProfile);
      params.append('iconFile', this.setIconFile);
      params.append('iconFileName', this.setIconFileName);

      let config = {
          headers: {'Content-Type': 'multipart/form-data'}
      };
      axios.post(this.$root.SERVER_PATH + 'updateSettingBase', params, config)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        this.$root.userData.userName = this.setUserName;
        this.$root.userData.profile = this.setProfile;
        this.$root.reloadValue = new Date().getTime();
      }).catch((error) => {
        console.log(error.response);
      });
    },
    pushUpdateFavorite : function () {
      this.errorMessageData.favorite = "";

      //トリム
      this.setFavoriteTag = this.setFavoriteTag.trim();
      //#がついていれば除去
      this.setFavoriteTag = this.$root.arrangeStr(this.setFavoriteTag, ',');
      
      let tagArray = this.setFavoriteTag.split(',');
      if (tagArray.length > 8) {
        this.setErrorMessageData.favorite = "お気に入りタグは8個まで登録できます";
        return;
      }
      for (let i = 0; i < tagArray.length; i++) {
        const element = tagArray[i];
        if (element.length > 20) {
          this.setErrorMessageData.favorite = "お気に入りタグは1つあたり20文字以下で入力してください";
          return;
        } else {
          let regStr = this.$root.checkRegulation(element)  
          if (regStr != "") {
            this.edErrorMessageData.favorite = this.$root.STR_ERROR_REGULATION + regStr;
          }
        }
      }
      
      //成型
      let favArray = this.setFavoriteTag.split(",");
      let favStr = "";
      for (let i = 0; i < favArray.length; i++) {
        //空文字を除去
        if (favArray[i] != "") {
          favStr = favStr + favArray[i];

          if (i != favArray.length - 1) {
            favStr = favStr + ",";
          }
        }        
      }
      this.setFavoriteTag = favStr;

      //前回と同じか
      if (this.setFavoriteTag == this.$root.userData.favorite) {
        return;
      }

      let params = new URLSearchParams();
      params.append('favorite', this.setFavoriteTag);

      axios.post(this.$root.SERVER_PATH + 'updateSettingFavorite', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        this.$root.userData.favorite = this.setFavoriteTag;

        this.popupKbn = "UPDATE";
          this.popupParams = {
            title : "お気に入りタグ",
            body : "変更しました",
          }
          this.$refs.popup.showModal();
      }).catch((error) => {
        console.log(error.response);
      });
      // this.setErrorMessageData.favorite = "お気に入りがだめー";
    },
    pushUpdateMute : function () {
      this.errorMessageData.mute = "";

      //トリム
      this.setMuteTag = this.setMuteTag.trim();
      //#がついていれば除去
      this.setMuteTag = this.$root.arrangeStr(this.setMuteTag, ',');
      
      let tagArray = this.setMuteTag.split(',');
      if (tagArray.length > 8) {
        this.setErrorMessageData.mute = "ミュートタグは8個まで登録できます";
        return;
      }
      for (let i = 0; i < tagArray.length; i++) {
        const element = tagArray[i];
        if (element.length > 20) {
          this.setErrorMessageData.mute = "ミュートタグは1つあたり20文字以下で入力してください";
          return;
        } else {
          let regStr = this.$root.checkRegulation(element)  
          if (regStr != "") {
            this.edErrorMessageData.mute = this.$root.STR_ERROR_REGULATION + regStr;
          }
        }
      }

      //成型
      let muteArray = this.setMuteTag.split(",");
      let muteStr = "";
      for (let i = 0; i < muteArray.length; i++) {
        //空文字を除去
        if (muteArray[i] != "") {
          muteStr = muteStr + muteArray[i];

          if (i != muteArray.length - 1) {
            muteStr = muteStr + ",";
          }
        }        
      }
      this.setMuteTag = muteStr;

      //前回と同じか
      if (this.setMuteTag == this.$root.userData.mute) {
        return;
      }

      let params = new URLSearchParams();
      params.append('mute', this.setMuteTag);

      axios.post(this.$root.SERVER_PATH + 'updateSettingMute', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        this.$root.userData.favorite = this.setFavoriteTag;

        this.popupKbn = "UPDATE";
        this.popupParams = {
          title : "ミュートタグ",
          body : "変更しました",
        }
        this.$refs.popup.showModal();
      }).catch((error) => {
        console.log(error.response);
      });
    },
    getSettingInfo : function () {
      //ミュートユーザーを取得
      let params = new URLSearchParams();
      
      axios.post(this.$root.SERVER_PATH + 'getSettingInfo', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        var resData = response.data;
        let muteList = resData["muteList"];
        this.setMuteUserList = {};
        for (let i = 0; i < muteList.length; i++) {
          let muteData = muteList[i];
          this.setMuteUserList[muteData["mute_id"]] = muteData["user_name"];
        }

        let baseInfo = resData["baseInfo"];

        //ユーザーの情報を取得
        this.setUserName = baseInfo["user_name"];
        this.setProfile = baseInfo["profile"];
        this.setFavoriteTag = baseInfo["favorite_tag"];
        this.setMuteTag = baseInfo["mute_tag"];
        this.setTwitterCode = baseInfo["twitter_code"];
        this.setMailAddress = baseInfo["mail_address"];
        if (baseInfo["show_twitter"] == "1") {
          this.setShowTwitter = true;
        } else {
          this.setShowTwitter = false;
        }

        console.log(this.setShowTwitter);

        this.setIconFileName = "";
        this.setIconImg = "";
        this.setIconFile = null;
        this.isChangeIcon = false;

        if (baseInfo["auth_mail"] == "0") {
          this.isAuthMail = false;
        } else {
          this.isAuthMail = true;
        }

        if (baseInfo["auth_twitter"] == "0") {
          this.isAuthTwitter = false;
        } else {
          this.isAuthTwitter = true;
        }
        
        this.errorMessageData = {
          icon : "",
          userName : "",
          profile : "",
          favorite : "",
          mute : ""
        }

        console.log(this.authMailAddress + "    AAAA");
      }).catch((error) => {
        console.log(error.response);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getSettingInfo();
      vm.$root.setCurrentPage("SETTING");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.getSettingInfo();
      vm.$root.setCurrentPage("SETTING");
    });
  }
}
</script>

