        console.log("Logout");
        if (flg) {
          this.pushMenu();
        }

        axios.get(this.CSRF_URL).then(response => {
          console.log(response);
          axios.get(this.$root.SERVER_PATH + 'logout')
          .then((response) => {
            let resData = response.data;
            console.log(resData["status"] );
          
            if (!this.$root.checkServerResponse(response)) {
              return;
            }
            
            signOut(getAuth());
            this.userData = {
              userId : "",
              userName : "",
              profile : "",
            };
            this.$router.push('/');
            this.isLogin = false;
          })
          .catch((e) => {
            alert(e);
          });
        });
      },
