          this.showPopup("TIPS", "▼シリーズ", "投稿した夢小説を「第１章、第２章...／第１話、第２話...」のようにシリーズとしてまとめることができます");
        } else if (index == 2) {
          this.showPopup("TIPS", "▼ジャンルタグ", "作家自身が作品ジャンル、作品傾向などをタグとして設定できます\nお気に入りタグ、ミュートタグ、検索などで使用します\n\n=====\n\nお気に入りタグ）\n読者がお気に入りのタグを登録できます\nお気に入りのタグは検索ページに一覧表示され、該当のジャンルタグのついた夢小説を探せます\n\nミュートタグ）\n読者がミュートタグを登録できます\n読者が苦手なジャンル、作品傾向を避けることができます\n\n検索）\n検索ボックスからジャンルタグを検索することができます\nまた、検索一覧や作品ページに表示されているジャンルタグから、該当する夢小説を探すことができます");
        } else if (index == 3) {
          this.showPopup("TIPS", "▼フィルター", "ワンクッション）\n心の準備をしてほしいときに「ワンクッション」のフィルターをかけることができます\n\nネタバレ）\n展開や結末などのネタバレが含まれている場合、「ネタバレ」のフィルターをかけることができます\n\nR-18）\nレーティングタグで「R-18」を指定していても不安な場合、「R-18」のフィルターをかけることができます\nレーティングタグを選択している場合、必須ではありません\n\nR-18G）\nレーティングタグで「R-18G」を指定していても不安な場合、「R-18G」のフィルターをかけることができます\nレーティングタグを選択している場合、必須ではありません\n\nパスワード）\nパスワードのヒント／パスワードを設定できます\nパスワードを設定した場合、自動的に新着、検索一覧に掲載されなくなります");
        } else if (index == 4) {
          this.showPopup("TIPS", "▼公開方法", "全体公開）\nyumedropにログインしているすべての読者に公開されます\n全体に公開していても、「新着、検索一覧に載せる」の項目で「載せない」とした場合、新着、検索一覧に掲載されなくなります\n\nTwitter相互フォロワー限定）\nyumedropにログインしているTwitterアカウントの相互フォロワーにのみ公開されます\nTwitter相互フォロワー限定にした場合、自動的に新着、検索一覧に掲載されなくなります\n\nTwitterフォロワー限定）\nyumedropにログインしているTwitterアカウントのフォロワーにのみ公開されます\nTwitterフォロワー限定にした場合、自動的に新着、検索一覧に掲載されなくなります\n\nTwitterリスト限定）\nyumedropにログインしているTwitterアカウントでリストに登録したユーザーにのみ公開されます\nTwitterリスト限定にした場合、自動的に新着、検索一覧に掲載されなくなります\n\nTwitterリツイート限定）\nyumedropにログインしているTwitterアカウントの該当投稿をリツイートしたユーザーにのみ公開されます\nTwitterリツイート限定にした場合、自動的に新着、検索一覧に掲載されなくなります\n\n非公開）\nすべての読者が読めなくなります\n非公開にした場合、自動的に新着、検索一覧に掲載されなくなります");
        } else if (index == 5) {
          this.showPopup("TIPS", "▼プロフィール画像", "プロフィール画像を設定できます\n自分自身が著作権を有していない作品の場合、該当の利用規約に従ってください");
        } else if (index == 6) {
          this.showPopup("TIPS", "▼お気に入りタグ", "読者がお気に入りのタグを登録できます\nお気に入りのタグは検索ページに一覧表示され、該当のジャンルタグのついた夢小説を探せます");
        } else if (index == 7) {
          this.showPopup("TIPS", "▼ミュートタグ", "読者がミュートタグを登録できます\n読者が苦手なジャンル、作品傾向を避けることができます");
        } else if (index == 8) {
          this.showPopup("TIPS", "▼ユーザーミュート", "自分と趣味趣向、性的嗜好が合わないユーザーをミュートすることができます\nユーザーミュートすると、新着、検索一覧に該当ユーザーが表示されなくなります\nユーザーミュートを解除したい場合、「設定＞ミュートユーザー＞確認する」から、該当ユーザーのページに移動し、ユーザーミュートボタンを押して解除してください");
        } else if (index == 9) {
          this.showPopup("TIPS", "▼変換項目", "夢小説に出てくる登場人物やキーワードを変換することができます\n入力欄に記入したワードは作者、運営に知られることはありません");
        }
      },
      retComSeriesList : function(dataList) {
        let seriesList = [];
        seriesList.push("すべて");

        for (let i = 0; i < dataList.length; i++) {
          var series = dataList[i].series;
          var flg = true;
          for (let j = 0; j < seriesList.length; j++) {
            if (seriesList[j] == series) {
              flg = false;
            }
          }
          if (flg) {
            seriesList.push(series);
          }
        }
        return seriesList;
      },
      retComFilterList : function(dataList, series) {
        let filterDataList = [];
        for (let i = 0; i < dataList.length; i++) {
          var data = dataList[i];
          //シリーズの絞り込み
          if (series == "すべて" || data.series == series) {
            filterDataList.push(data);
          }
        }
        return filterDataList;
      },
      retComPagingList : function (page, dataList) {
        let retArray = [];
        if (dataList.length == 0) {
          return retArray;
        }

        let max = (page - 1) * 10 + 10;
        if (dataList.length < (page - 1) * 10 + 10) {
          max = dataList.length;
        }

        for (let i = (page - 1) * 10; i < max; i++) {
          retArray.push(dataList[i]);
        }
        return retArray;
      },
      checkAddress : function (mailAddress) {
        if (mailAddress.indexOf('@') == -1) {
          return 1;
        } else {
          let domain = mailAddress.split('@')[1];

          if (domain.indexOf('.jp') == -1 && domain.indexOf('.com') == -1 && domain.indexOf('.net') == -1 && 
            domain.indexOf('.org') == -1 && domain.indexOf('.xyz') == -1) {
            return 2;
          }
        }
        return 0;
      },
      checkPassword : function (password) {
        const regex = /^[a-zA-Z0-9!?&$%+-/#]+$/;
        if (regex.test(password)) {
          return 0;
        } else {
          return 1;
        }
      },
      checkRegulation : function (str) {
        for (let i = 0; i < this.regulationArray.length; i++) {
          if (str.indexOf(this.regulationArray[i]) != -1) {
            return this.regulationArray[i];
          }
        }
        return "";
      }
    }
  }
</script>

