<template>
  <div class="reading container-fluid" :class="this.$root.theme_bc_2">
    <div style="height:18px" />
    <div class="mainArea" style="text-align:left; margin:auto;"> 
      <div class="main">
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px;">
          <post-list-row v-bind:rowData='rowData'></post-list-row>
          <div v-if="reConversionList != null" class="container-fluid">
            <span class="p14">変換項目<img class="cursor tips" v-on:click="this.$root.pushTips(9)" :src="this.$root.IMG_PATH + 'btn_hint.png'"></span>
            <div v-for="(conversion, index) in reConversionList" :key="index">
              <span class="p14">{{reConversionList[index][0]}}</span><br>
              <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="reConversionList[index][2]" style="width:100%"  placeholder="変換する文字を入力してください" maxlength="10">
            </div>
            <div style="height:12px" />
            <p class="errMsg" v-if="errorMessageData.conversion"><br>{{errorMessageData.conversion}}</p>
            <span class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushConversion">名前変換</span>
            <div class="grayBar" />
          </div>
          <div v-show="publishingStatus == 0 && filterStatus == 0">
            {{body}}
          </div>
          <div v-show="filterStatus != 0 || publishingStatus != 0" style="padding:20px 20px; text-align:center">
            <img v-show="filterStatus != 0" :src="filterImgPath" style="width:104px; height:129px">
            <img v-show="publishingStatus != 0" :src="publishingImgPath" style="width:200px;">
            <div style="height:12px" />
            <div v-show="publishingStatus == 1">
              <!-- パスワード -->
              <p>{{publishingPasswordHint}}</p>
              <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" maxlength="10" v-model="publishingPassword" style="width:100%" placeholder="パスワードを入力してください">
            </div>
            <span v-show="isDispBtn" class="btn" :class="this.$root.theme_bc_1" style="width:145px" v-on:click="pushAgreement">全て表示</span>
          </div>
        </div>
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px; margin-top:18px">
          <span class="p14">愛されスタンプ</span>
          <div v-show="this.stampList.length != 0">
            <img v-for="(stName, index) in stampList" :key="index" :src="this.$root.IMG_PATH + 'stamp/' + stName + '.png'" style="width:30px; height:30px">
          </div>
          <div v-show="this.stampList.length == 0" style="height:30px" />
          <div class="grayBar" />
          <span class="p14">リアクションをたくさん送って愛を伝えよう！</span>
          <div>
            <a v-for="(stName, index) in defStampList" :key="index" href="#" v-on:click.prevent.stop="pushStamp(stName)" >
              <img :src="this.$root.IMG_PATH + 'stamp/' + stName + '.png'" style="width:30px">
            </a>
          </div>
        </div>
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px; margin-top:18px">
          <post-list-row v-bind:rowData='rowData'></post-list-row>
          <span class="btn btn-on btn-icon btn-icon-bookmark-on" :class="[this.$root.theme_boc_1, this.$root.theme_fc_1, theme_btn_icon_bookmark_on]" style="width:145px" v-show="isBookmark == 0" v-on:click="pushBookmark(1)">本棚に追加</span>
          <span class="btn btn-icon btn-icon-bookmark-off" :class="this.$root.theme_bc_1" style="width:145px" v-show="isBookmark == 1" v-on:click="pushBookmark(0)">本棚から削除</span>
        </div>
      </div>
      <ad-area></ad-area>
    </div>
  </div>
  <popup-base ref="popup" @popupAction="popupAction" :kbn="this.popupKbn" :params="this.popupParams"></popup-base>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Reading',
  components: {
  },
  data() {
    return {
      reConversionList : null,
      title : "",
      preBody : "",
      body : "",
      rowData : null,
      isBookmark : 0,
      stampList : [],
      filterStatus : 0,
      publishingStatus : 0,
      publishingPassword : "",
      publishingPasswordHint : "",

      defStampList : [
        "st001",
        "st002",
        "st003",
        "st004",
        "st005",
        "st006",
        "st007",
        "st008",
        "st009",
        "st010"
        ],

      errorMessageData : {
        conversion : "",
      },

      popupKbn : "",
      popupParams : {
        title : "",
        body : "",
        tweet : "",
      },

      isLimit : false,
      isDispBtn : true,
    };
  },
  computed : {
    filterImgPath : function () {
      console.log(this.filterStatus);
      if (this.filterStatus == 1) {
        return this.$root.IMG_PATH + "fil_r18.png";
      } else if (this.filterStatus == 2) {
        return this.$root.IMG_PATH + "fil_r18g.png";
      } else if (this.filterStatus == 3) {
        return this.$root.IMG_PATH + "fil_cushion.png";
      } else if (this.filterStatus == 4) {
        return this.$root.IMG_PATH + "fil_netabare.png";
      }
      return "";
    },
    publishingImgPath : function () {
      console.log(this.publishingStatus);
      if (this.publishingStatus == 1) {
        return this.$root.IMG_PATH + "pub_password.png";
      } else if (this.publishingStatus == 2) {
        return this.$root.IMG_PATH + "pub_sougo.png";
      } else if (this.publishingStatus == 3) {
        return this.$root.IMG_PATH + "pub_follower.png";
      } else if (this.publishingStatus == 4) {
        return this.$root.IMG_PATH + "pub_list.png";
      }
      return "";
    },
    theme_btn_icon_bookmark_on : function() {
      if (this.$root.themeIndex == 0) {
          return "btn-icon-bookmark-on_1";
        } else if (this.$root.themeIndex == 1) {
          return "btn-icon-bookmark-on_2";
        } else if (this.$root.themeIndex == 2) {
          return "btn-icon-bookmark-on_3";
        }
        return "btn-icon-bookmark-on_1";
    },
  },
  methods : {
    pushBookmark : function(val) {
      let params = new URLSearchParams();
      params.append('postId', this.$route.params.postId);
      params.append('value', val);
      axios.post(this.$root.SERVER_PATH + 'updateBookmark', params)
      .then((response) => {
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }
        this.isBookmark = val;
      });
    },
    pushAgreement : function() {
      if (this.publishingStatus == 0) {
        //フィルターだけの場合
        this.filterStatus = 0;
        return;
      } else {
        if (this.$root.userData.isAuthTwitter == false) {
          //twitter連携をしていない場合
          this.popupKbn = "PUBLISH";
          this.popupParams = {
            title : "Twitter連携",
            body : "この作品はTwitter連携が必要です\n設定からTwitter連携を行ってください",
          }
          this.$refs.popup.showModal();
          return;
        }

        let params = new URLSearchParams();
        params.append('postId', this.$route.params.postId);

        //公開方法別チェック
        if (this.publishingStatus == 1) {
          //パスワード
          params.append('kbn', "PASSWORD");
          params.append('password', this.publishingPassword);
        } else if (this.publishingStatus == 2) {
          //相互フォロー
          params.append('kbn', "TWITTER_SOUGO");
          params.append('userId', this.rowData.userId);
       } else if (this.publishingStatus == 3) {
          //フォロー
          params.append('kbn', "TWITTER_FOLLOW");
          params.append('userId', this.rowData.userId);
        } else if (this.publishingStatus == 4) {
          //リスト
          params.append('kbn', "TWITTER_LIST");
        }

        axios.post(this.$root.SERVER_PATH + 'checkPublishing', params)
        .then((response) => {
          if (!this.$root.checkServerResponse(response, true)) {
            return;
          }
          let resData = response.data;
          if (resData["status"] == this.$root.API_SUCCESS) {
            //OK
            let body = resData["body"];
            this.body = body;            
            this.preBody = this.body;
            this.publishingStatus = 0;
            this.filterStatus = 0;
          } else {
            //NG
            let errBody = "";
            let errTitle = "Twitter連携エラー";
            let errorNo = resData["error"];
            if (errorNo == "L1") {
              //メンバーじゃない
              errBody = "指定された公開リストのメンバーではありません";
            } else if (errorNo == "L2") {
              //リスト存在なし
              errBody = "著者が指定したリストが存在しません";
            } else if (errorNo == "P1") {
              //パスワード不一致
              errTitle = "パスワードエラー";
              errBody = "パスワードが間違っています";
            } else if (errorNo == "S1") {
              //相互不一致
              errBody = "著者のTwitterアカウントと相互フォローではありません";
            } else if (errorNo == "F1") {
              //フォロー不一致
              errBody = "著者のTwitterアカウントをフォローしていません";
            } else if (errorNo == "99") {
              //api取得上限
              this.popupKbn = "PUBLISHING";
              this.popupParams = {
              title : this.$root.STR_TITLE_TWITTER_API_LIMIT,
              body : this.$root.STR_BODY_TWITTER_API_LIMIT,
              }
              this.$refs.popup.showModal();
              this.isDispBtn = false;
              return;
            }
            this.popupKbn = "PUBLISH";
            this.popupParams = {
              title : errTitle,
              body : errBody,
            }
            this.$refs.popup.showModal();

            if (this.publishingStatus == 2 || this.publishingStatus == 3 || this.publishingStatus == 4) {
              this.isDispBtn = false;
            }
            return;
          }
        });
      }

    },
    getReadingData : function() {
      let params = new URLSearchParams();
      params.append('postId', this.$route.params.postId);
      axios.post(this.$root.SERVER_PATH + 'getReadingData', params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_FAILED_NODATA) {
          //該当データなし
          this.$router.replace({path : "/error", query : {errorCode : resData["status"], message : "該当の投稿は存在しません"}});
          return;
        }
        
        if (!this.$root.checkServerResponse(response, true)) {
          return;
        }

        let postData = resData["postData"];
        var tagStr = postData["tags"];
        var tagArray = {};
        if (tagStr != "") {
          tagArray = tagStr.split(',');
        }
        console.log("IN");
        this.rowData = {
          postId : postData["post_id"],
          userId : postData["user_id"],
          listType : "listTypeD",
          author : postData["user_name"],
          series : postData["series"],
          title : postData["title"],
          outline : postData["outline"],
          rating : postData["rating"],
          chara : postData["chara"],
          creation : postData["creation"],
          tags : tagArray,
          length : postData["length"],
          postDate : new Date(postData["created_at"]),
        };

        this.isBookmark = parseInt(postData.bookmark);
        this.title = postData["title"];

        
        this.stampList = [];
        let stList = resData["stamp"];
        for (let i = 0; i < stList.length; i++) {
          this.stampList.push(stList[i]["stamp_id"]);
        }

        //フィルター
        this.filterStatus = postData["filter"];

        //公開方法
        if (this.$root.userData.userId == postData["user_id"]) {
          this.pusblishing = 0;
          this.body = postData["body"];
          this.preBody = this.body;
        } else {
          this.publishingStatus = postData["publishing"];
          console.log(this.filterStatus + "  " + this.publishingStatus);
          if (this.publishingStatus == 1) {
            this.publishingPasswordHint = "ヒント：" + postData["publishing_sub2"];
          }
        }

        //変換項目
        this.reConversionList = null;
        var conversionStr = postData["conversion"];
        if (conversionStr != "") {
          this.reConversionList = [];
          var cArray1 = conversionStr.split(this.$root.CONVERSION2);
          for (let i = 0; i < cArray1.length; i++) {
            var cArray2 = cArray1[i].split(this.$root.CONVERSION1);
            this.reConversionList.push([cArray2[0], cArray2[1], ""]);       
          }
        }
      });
    },
    pushConversion : function() {
      this.errorMessageData.conversion = "";

      for (let i = 0; i < this.reConversionList.length; i++) {
        if (this.reConversionList[i][2] == "") {
          this.errorMessageData.conversion = "変換文字は1文字以上入力してください";
          return;
        } else if (this.reConversionList[i][2].length > 10) {
          this.errorMessageData.conversion = "変換文字は10文字以下で入力してください";
          return;
        }  else {
          let regStr = this.$root.checkRegulation(this.reConversionList[i][2])  
          if (regStr != "") {
            this.edErrorMessageData.conversion = this.$root.STR_ERROR_REGULATION + regStr;
            return;
          }
        }
      }

      var body = this.preBody;
      for (let i = 0; i < this.reConversionList.length; i++) {
        body = body.replace(this.reConversionList[i][1], this.reConversionList[i][2]);
      }
      this.body = body;
    },
    pushStamp : function (stName) {
      if (this.isLimit) {
          this.popupKbn = "STAMP";
          this.popupParams = {
            title : "",
            body : "1作品に押せるスタンプは10個までです",
          }
          this.$refs.popup.showModal();
          this.isLimit = true;
          return;
      }

      let params = new URLSearchParams();
      params.append('postId', this.$route.params.postId);
      params.append('userId', this.$root.userData.userId);
      params.append('stamp', stName);
      axios.post(this.$root.SERVER_PATH + 'addStamp', params)
      .then((response) => {
        if (response.data["status"] == this.$root.API_FAILED_LIMIT) {
          this.popupKbn = "STAMP";
          this.popupParams = {
            title : "スタンプ上限",
            body : "1作品に押せるスタンプは10個までです",
          }
          this.$refs.popup.showModal();
          this.isLimit = true;
          return;
        } else if (!this.$root.checkServerResponse(response, true)) {
          return;
        }

        this.stampList.push(stName);
      }).catch((error) => {
        console.log(error.response);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getReadingData();
      vm.$root.setCurrentPage("READING");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.getReadingData();
      vm.$root.setCurrentPage("READING");
    });
  }
}
</script>

